import React, { PureComponent } from 'react';
import { withStyles } from '@material-ui/styles';

import { Page, SearchBar } from 'components';
import { Header, Results } from './components';
import * as actions from 'actions';
import { connect } from 'react-redux';
import { Skeleton } from '@material-ui/lab';
import { pathUrls } from 'config';
import {withRouter} from 'react-router-dom'

const styles = theme => ({
  root: {
    padding: theme.spacing(3)
  },
  results: {
    marginTop: theme.spacing(3)
  }
});

class UsersList extends PureComponent {
  state = {
    users : null,
    page:1,
    offset:0,
    limit:10,
    pages:0
  };

  componentDidMount() {
    const { match } = this.props;
    const { page } = match.params;
    const pageNo = page ? Number(page) : 1;
    if(pageNo){
      let offset = (pageNo - 1)*this.state.limit;
      this.setState({page:pageNo, offset:offset})
    }

    if(!this.state.users && pageNo === 1){
      this.props.onUsersList(this.state.limit, this.state.offset)
    }

  }

  componentWillReceiveProps(nextProps) {
    if (this.props !== nextProps) {
      if(nextProps.count.total !== null && this.props.list.users !== nextProps.list.users){
        let pages = Math.ceil(nextProps.count.total/this.state.limit)
        this.setState({users:nextProps.list.users, pages: pages, count:nextProps.count.total})
      }
      if(nextProps.delete.deleted){
        if(nextProps.delete.users !== this.props.delete.users){
          let users = this.state.users
          users.map((item, index)=>{
            if(nextProps.delete.users.includes(item.id)){
              delete users[index];
            }
          });
          this.setState({users:users})

        }
      }else if(nextProps.suspend.suspended !== this.props.suspend.suspended){
        let suspended = !!nextProps.suspend.suspended
        if(nextProps.suspend.users !== this.props.suspend.users){
          let users = this.state.users
          users.map((item, index)=>{
            if(nextProps.suspend.users.includes(item.id)){
              users[index].suspended = suspended;
            }
          });
          this.setState({users:users})

        }
      }
    }
  }

  componentWillUpdate(nextProps, nextState) {
    if(this.state.page !== nextState.page && this.state.offset !== nextState.offset){
      this.props.onUsersList(nextState.limit, nextState.offset)
    }
  }

  changePageUrl = (pageNo)=>{
    const { history } = this.props;
    let url = pathUrls.users.listPagePath.replace(':page', pageNo);
    history.push(url)
  };

  handlePageChange = (event, pageNo)=>{
    let offset = (pageNo - 1)*this.state.limit;
    this.setState({page:pageNo, offset:offset})
    this.changePageUrl(pageNo)
  };

  handleNextPage = ()=>{
    let page = this.state.page + 1;
    let offset = (page - 1)*this.state.limit;
    this.setState({page:page, offset:offset})
    this.changePageUrl(page)
  };

  handlePrevPage = ()=>{
    let page = this.state.page - 1;
    let offset = (page - 1)*this.state.limit;
    this.setState({page:page, offset:offset})
    this.changePageUrl(page)
  };

  handleMenuSelect =(type, user_id) => {
    const { history } = this.props;
    if(type === 'delete'){
      this.props.onDelete(user_id);
    }else if(type === 'suspend'){
      this.props.onSuspend(user_id, true);
    }else if(type === 'unsuspend'){
      this.props.onSuspend(user_id, false);
    }else if(type === 'update'){
      history.push(pathUrls.user.update.getPath(user_id))
    }else if(type === 'view'){
      history.push(pathUrls.user.view.getPath(user_id))
    }
  };

  // handleSearch(){
  //
  // }

  loading = ()=>{
    let skelton = [];
    skelton.push(<Skeleton
      variant="text"
      width={150}
                 />)
    let data = Array(10).fill(1).map((item)=>{
      return <Skeleton
        height={50}
        variant="text"
      />
    });
    return [...skelton, ...data]
  };

  results = ()=>{
    const { classes } = this.props;
    if(this.state.users){
      return (
        <div>
          <Results
            className={classes.results}
            count={this.state.count}
            limit={this.state.limit}
            onMenuClick={this.handleMenuClick}
            onNextChange ={this.handleNextPage}
            onPageChange ={this.handlePageChange}
            onPrevChange ={this.handlePrevPage}
            onSelect={this.handleMenuSelect}
            page={this.state.page}
            pages={this.state.pages}
            users={this.state.users}
          />
        </div>
      )
    }
  };

  display = ()=>{
    if(this.props.list.loading){
      return this.loading()
    }else {
      return this.results()
    }
  };

  render() {
    const { classes } = this.props;

    return (
      <Page
        className={classes.root}
        title="Users List"
      >
        <Header />
        {this.display()}
      </Page>
    );
  }
}

const mapStateToProps = state => {
  return {
    list: state.users.list,
    delete: state.users.delete,
    suspend: state.users.suspend,
    count: state.users.count,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onDelete: (users) => dispatch( actions.deleteUsers(users) ),
    onSuspend: (users, suspend) => dispatch( actions.suspendUsers(users, suspend) ),
    onUsersList: (limit, offset) => dispatch( actions.getUserList(limit, offset) ),
  };
};
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(UsersList)));
