import React, { useState } from 'react'
import { Link as RouterLink } from 'react-router-dom'
import clsx from 'clsx'
import PropTypes from 'prop-types'
import PerfectScrollbar from 'react-perfect-scrollbar'
import { makeStyles } from '@material-ui/styles'
import Pagination from '../Pagination'
import {
  Card,
  CardActions,
  CardContent,
  CardHeader,
  Checkbox,
  Divider,
  Link,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
  Button,
  colors
} from '@material-ui/core'

import { Label } from 'components'
import { pathUrls } from '../../../../../config';

const useStyles = makeStyles(theme => ({
  root: {},
  content: {
    padding: 0
  },
  inner: {
    minWidth: 700
  },
  nameCell: {
    display: 'flex',
    alignItems: 'center'
  },
  avatar: {
    height: 42,
    width: 42,
    marginRight: theme.spacing(1)
  },
  actions: {
    padding: theme.spacing(1),
    justifyContent: 'flex-end'
  },
  dangerButton: {
    marginRight: theme.spacing(1),
    color: colors.red[600],
    borderColor: colors.red[600],
    '&:hover': {
      borderColor: colors.red[900],
      color: colors.red[900]
    }
  },
  primaryButton: {
    marginRight: theme.spacing(1),
    color: colors.blue[600],
    borderColor: colors.blue[600],
    '&:hover': {
      borderColor: colors.blue[900],
      color: colors.blue[900]
    }
  },
  successButton: {
    marginRight: theme.spacing(1),
    color: colors.green[600],
    borderColor: colors.green[600],
    '&:hover': {
      borderColor: colors.green[900],
      color: colors.green[900]
    }
  }
}))

const Results = props => {
  const { className, count, page, pages, limit, users, ...rest } = props

  const classes = useStyles()

  const [selectedCustomers, setSelectedCustomers] = useState([])

  const handleSelectAll = event => {
    const selectedCustomers = event.target.checked
      ? users.map(user => user.id)
      : []

    setSelectedCustomers(selectedCustomers)
  }

  const handleSelectOne = (event, id) => {
    const selectedIndex = selectedCustomers.indexOf(id)
    let newSelectedCustomers = []

    if (selectedIndex === -1) {
      newSelectedCustomers = newSelectedCustomers.concat(selectedCustomers, id)
    } else if (selectedIndex === 0) {
      newSelectedCustomers = newSelectedCustomers.concat(
        selectedCustomers.slice(1)
      )
    } else if (selectedIndex === selectedCustomers.length - 1) {
      newSelectedCustomers = newSelectedCustomers.concat(
        selectedCustomers.slice(0, -1)
      )
    } else if (selectedIndex > 0) {
      newSelectedCustomers = newSelectedCustomers.concat(
        selectedCustomers.slice(0, selectedIndex),
        selectedCustomers.slice(selectedIndex + 1)
      )
    }
    setSelectedCustomers(newSelectedCustomers)
  }

  const clickActions = {
    onView: () => {
      props.onSelect('view', selectedCustomers[0])
    },
    onUpdate: () => {
      props.onSelect('update', selectedCustomers[0])
    },
    onSuspend: () => {
      props.onSelect('suspend', selectedCustomers)
      setSelectedCustomers([])
    },
    onUnSuspend: () => {
      props.onSelect('unsuspend', selectedCustomers)
      setSelectedCustomers([])
    },
    onDelete: () => {
      props.onSelect('delete', selectedCustomers)
      setSelectedCustomers([])
    }
  }

  const btnViews = {
    view: (
      <Button
        className={classes.primaryButton}
        color="primary"
        onClick={clickActions.onView}
        size="small"
        type="submit"
        variant="outlined"
      >
        View
      </Button>
    ),
    update: (
      <Button
        className={classes.primaryButton}
        color="primary"
        onClick={clickActions.onUpdate}
        size="small"
        type="submit"
        variant="outlined"
      >
        Update
      </Button>
    ),
    suspend: (
      <Button
        className={classes.dangerButton}
        color="primary"
        onClick={clickActions.onSuspend}
        size="small"
        type="submit"
        variant="outlined"
      >
        Suspend
      </Button>
    ),
    unSuspend: (
      <Button
        className={classes.successButton}
        color="primary"
        onClick={clickActions.onUnSuspend}
        size="small"
        type="submit"
        variant="outlined"
      >
        Unsuspend
      </Button>
    ),
    delete: (
      <Button
        className={classes.dangerButton}
        color="primary"
        onClick={clickActions.onDelete}
        size="small"
        type="submit"
        variant="outlined"
      >
        Delete
      </Button>
    )
  }

  const userTypeColor = (role) => {
    switch (role) {
      case 'admin': return colors.grey[900]
      case 'buyer': return colors.blue[900]
      case 'seller': return colors.green[900]
    }
  }

  const getUserLable = (role) => {
    if (role) {
      return role.map((value) => {
        return (
          <Label
            color={userTypeColor(value.toLowerCase())}
          >
            {value.toLowerCase()}
          </Label>
        )
      })
    } else {
      return (
        <Label
          color={colors.red[900]}
        >
          undefined
        </Label>
      )
    }
  }
  let btnView = null
  if (selectedCustomers.length === 1) {
    btnView = (
      <React.Fragment>
        {btnViews.view}
        {btnViews.update}
        {btnViews.unSuspend}
        {btnViews.suspend}
        {btnViews.delete}
      </React.Fragment>
    )
  } else if (selectedCustomers.length > 1) {
    btnView = (
      <React.Fragment>
        {btnViews.unSuspend}
        {btnViews.suspend}
        {btnViews.delete}
      </React.Fragment>
    )
  }
  return (
    <div
      {...rest}
      className={clsx(classes.root, className)}
    >
      <Typography
        color="textSecondary"
        gutterBottom
        variant="body2"
      >
        {count} Records found. Page {props.page} of{' '} {pages}
      </Typography>
      <Card>
        <CardHeader
          action={btnView}
          title="All Users"
        />
        <Divider />
        <CardContent className={classes.content}>
          <PerfectScrollbar>
            <div className={classes.inner}>
              <Table
                aria-label="a dense table"
                size="small"
              >
                <TableHead>
                  <TableRow>
                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={selectedCustomers.length === users.length}
                        color="primary"
                        indeterminate={
                          selectedCustomers.length > 0 &&
                          selectedCustomers.length < users.length
                        }
                        onChange={handleSelectAll}
                      />
                    </TableCell>
                    <TableCell>Name</TableCell>
                    <TableCell>Email</TableCell>
                    <TableCell>Phone Number</TableCell>
                    <TableCell>Status</TableCell>
                    <TableCell>Role</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {users.map(user => (
                    <TableRow
                      hover
                      key={user.id}
                      selected={selectedCustomers.indexOf(user.id) !== -1}
                    >
                      <TableCell padding="checkbox">
                        <Checkbox
                          checked={
                            selectedCustomers.indexOf(user.id) !== -1
                          }
                          color="primary"
                          onChange={event =>
                            handleSelectOne(event, user.id)
                          }
                          value={selectedCustomers.indexOf(user.id) !== -1}
                        />
                      </TableCell>
                      <TableCell>
                        <div className={classes.nameCell}>
                          <div>
                            <Link
                              color="inherit"
                              component={RouterLink}
                              to={pathUrls.users.viewPath.replace(':id', user.id)}
                              variant="h6"
                            >
                              {user.firstName} {user.lastName}
                            </Link>
                            <div>
                              {user.email}
                            </div>
                          </div>
                        </div>
                      </TableCell>
                      <TableCell>
                        {user.email}
                      </TableCell>
                      <TableCell>{user.phoneNumber}</TableCell>
                      <TableCell>
                        <Label
                          color={
                            user.suspended ? colors.red[600] : colors.green[600]
                          }
                        >
                          {user.suspended
                            ? 'Suspended'
                            : 'Active'}
                        </Label>
                      </TableCell>
                      <TableCell>
                        {getUserLable(user.role)}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          </PerfectScrollbar>
        </CardContent>
        {pages > 1 && (
          <CardActions className={classes.actions}>
            <Pagination
              count={count}
              limit={limit}
              onChangePage={props.onPageChange}
              onNextPage={props.onNextChange}
              onPrevPage={props.onPrevChange}
              page={page}
              pages={pages}
            />
          </CardActions>
        )}
      </Card>
    </div>
  )
}

Results.propTypes = {
  className: PropTypes.string,
  users: PropTypes.array.isRequired
}

Results.defaultProps = {
  users: []
}

export default Results
